import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue'
import empty from "@/components/empty";
// import Json from '@/Json';
import {
	mapGetters,
	mapMutations
} from "vuex"
export default {
	components: {
		empty,
		uniLoadMore,
		uniPopup
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [],
			maskFlag: false, //弹窗控制变量
			pageData: {
				page: 1,
				limit: 3,
				status: 0 //全部
			}, // 分页信息
			selectedOrder: {}, //选中订单
		};
	},

	onLoad(options) {
		// this.loadData();
		// this.getOrderMenu(); //获取订单菜单列表			
	},
	onShow() {
		this.isLogin(); //判断是否登录
		this.close();
		this.pageData = {
			page: 1,
			limit: 3,
			status: 0 //全部
		};
	},
	methods: {
		...mapMutations({
			setReturnOrder: 'setReturnOrder',
			setAddressData: 'setAddressData',
			setOrderData: 'setOrderData',
			setOrderState: 'setOrderState'
		}),
		// 店铺首页
		toStoreHome(e) {
			console.log(e);
			uni.setStorageSync('factory_id', e.factory_id);
			uni.navigateTo({
				url: "/pages/index/storeHome/storeHome"
			})
		},
		// 订单详情页
		toOrderDetail(item) {
			this.$api.orderDetail({
				order_id: item.order_id
			}).then(res => {
				if (res.data.errno == 0) {
					console.log(res);
					let data = res.data.data;
					// 设置vuex的收货地址信息
					this.setAddressData({
						address: data.address,
						address_id: "",
						name: data.name,
						phone: data.phone
					})
					this.setOrderData(data); //vuex设置订单信息
					this.setOrderState(data.status); //vuex设置订单状态
					uni.navigateTo({
						url: "/pages/index/confirmOrder/confirmOrder"
					})
				} else {

				}
			})
		},
		// 确认收货
		confirmReceipt(item) {
			this.$api.orderFinish({
				order_id: item.order_id
			}).then(res => {
				uni.showToast({
					title: res.data.msg,
					duration: 1000,
					icon: "none"
				});
				if (res.data.errno == 0) {
					this.navList[this.tabCurrentIndex].orderList = [];
					this.getOrderList(this.tabCurrentIndex);
				}
				console.log(res);
			})
		},
		// 判断是否登录
		isLogin() {
			this.userInfo = uni.getStorageSync('user_info')
			if (this.userInfo.token) {
				this.getOrderMenu();
			} else {
				uni.showToast({
					title: '还未登录，请先登录',
					duration: 1000,
					icon: 'none'
				})
				setTimeout(res => {
					uni.switchTab({
						url: '/pages/mine/mine'
					})
				}, 1000)
			}
		},
		//获取订单菜单列表
		getOrderMenu() {
			this.$api.orderMenu().then(res => {
				if (res.data.errno == 0) {
					this.navList = res.data.data;
					this.tabCurrentIndex = this.navList[0].status;
					this.navList.map((item, index) => {
						item.loadingType = 'more';
						item.orderList = [];
					})
					this.getOrderList(this.tabCurrentIndex); //获取订单列表数据
				} else if (res.data.errno === 400 || res.data.errno === 4008 || res.data.errno === 4009) {
					uni.showToast({
						title: '登录过时，请重新登录',
						duration: 1000,
						icon: 'none'
					})

					uni.setStorageSync('user_info', {
						token: '',
						user_id: '',
						user_img: '../../static/img/mine/default-img.png',
						user_name: '点击头像登录'
					});

					setTimeout(() => {
						uni.navigateTo({
							url: '/pages/mine/mine'
						})
					}, 1000)
				}
			})
		},
		// 获取订单列表
		getOrderList(index) {
			this.pageData.status = index
			this.navList[index].loadingType = "loading";
			this.$api.orderList(this.pageData).then(res => {
				if (res.data.errno == 0) {
					console.log(res);
					if (res.data.data.next_page == 0) {
						this.navList[index].loadingType = "noMore";
					} else {
						this.pageData.page = res.data.data.next_page;
						this.navList[index].loadingType = "more";
					}
					this.$set(this.navList[index], 'orderList', [...this.navList[index].orderList, ...res.data.data.list]);
					this.$forceUpdate();
				}
			})
		},
		// 加载更多
		loadMore(index) {
			if (this.navList[index].loadingType == "noMore") {
				console.log("没有更多了")
				return
			}
			this.getOrderList(index);
		},
		//swiper 切换
		changeTab(e) {
			console.log(e);
			this.tabCurrentIndex = e.target.current;
			this.navList[this.tabCurrentIndex].orderList = [];
			this.pageData = {
					page: 1,
					limit: 3,
					status: 0 //全部
				},
				this.getOrderList(this.tabCurrentIndex);
		},
		//顶部tab点击
		tabClick(index) {
			this.navList[index].orderList = [];
			this.tabCurrentIndex = index;
		},
		//删除订单
		deleteOrder(index) {
			uni.showLoading({
				title: '请稍后'
			})
			setTimeout(() => {
				this.navList[this.tabCurrentIndex].orderList.splice(index, 1);
				uni.hideLoading();
			}, 600)
		},
		isCancelOrder(item) {
			this.selectedOrder = item;
			this.$refs['remaks'].open();
		},
		// 取消按钮
		close() {
			this.$refs['remaks'].close();
		},
		// 申请退款
		requestRefund(item) {
			console.log(item);
			this.setReturnOrder(item);
			uni.navigateTo({
				url: "/pages/order/refundReturn"
			})
		},
		//取消订单
		cancelOrder() {
			this.$refs['remaks'].close();
			uni.showLoading({
				title: '请稍后'
			})
			this.$api.orderCancel({
				order_id: this.selectedOrder.order_id
			}).then(res => {
				if (res.data.errno == 0) {
					uni.hideLoading();
					uni.showToast({
						title: '订单取消成功',
						duration: 1000,
						icon: "none"
					});
					this.navList[this.tabCurrentIndex].orderList = [];
					this.getOrderList(this.tabCurrentIndex);
				}
			})

		},
		// 订单支付
		payOrder(item) {
			let code = "";
			uni.login({
				provider: 'weixin',
				success: (loginRes) => {
					console.log(loginRes);
					if (loginRes.errMsg == "login:ok") {
						code = loginRes.code;
						this.$api.orderPay({
							order_id: item.order_id,
							js_code: code
						}).then(res => {
							if (res.data.errno == 0) {
								console.log(res);
								let data = res.data.data;
								uni.requestPayment({
									provider: 'wxpay',
									timeStamp: data.timeStamp,
									nonceStr: data.nonceStr,
									package: data.package,
									signType: data.signType,
									paySign: data.paySign,
									success: function(res) {
										console.log('success:' + JSON.stringify(res));
										uni.showToast({
											title: '支付成功',
											duration: 1500,
											icon: "none"
										});
										uni.navigateTo({
											url: "/pages/index/paySuccess/paySuccess"
										})
									},
									fail: function(err) {
										console.log('fail:' + JSON.stringify(err));
										uni.showToast({
											title: '支付失败',
											duration: 1500,
											icon: "none"
										});
										uni.navigateTo({
											url: "/pages/index/payFail/payFail"
										})
									}
								});
							}
						})


					}
				}
			});

		},
		//订单状态文字和颜色
		orderStateExp(state) {
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch (+state) {
				case 1:
					stateTip = '待付款';
					break;
				case 2:
					stateTip = '待发货';
					break;
				case 9:
					stateTip = '订单已关闭';
					stateTipColor = '#909399';
					break;

					//更多自定义
			}
			return {
				stateTip,
				stateTipColor
			};
		}
	},
}
